











































import Vue from 'vue';
import Title from '../../components/common/Title.vue';
import '@toast-ui/editor/dist/toastui-editor.css';
import { Editor } from '@toast-ui/vue-editor';
import { Post } from '../../types/index';
import { editPost } from '../../api/posts';
import { mapState } from 'vuex';

export default Vue.extend({
	components: { Title, Editor },

	data: () => ({
		valid: true,
		datetime: new Date().toISOString().substr(0, 10),
		calendar: false,
		title: '',
		titleRules: [
			v => !!v || '제목을 입력해주세요!',
			v => (v && v.length <= 20) || '제목은 25자 이하로 작성해주세요!',
		],
		board: '자유게시판',
		boards: ['자유게시판'],
		editorOptions: {
			hideModeSwitch: true,
			usageStatistics: false,
		},
		content: '',
	}),

	computed: {
		...mapState(['username', 'token', 'posts']),
	},

	created() {
		const post = this.posts.filter(post => this.$route.params.id == post.id)[0];
		this.title = post.title;
		this.content = post.content;
	},

	methods: {
		async submitForm() {
			if (!this.$refs.form.validate()) return;
			const submitData = {
				id: this.$route.params.id,
				token: this.token,
				title: this.title,
				content: this.$refs.toastuiEditor.invoke('getMarkdown') || '',
			};

			try {
				const res = await editPost(submitData);
				this.$store.commit('openSnackbar', '게시물이 수정되었습니다!.');
				this.$router.replace('/board/list');
			} catch (error) {
				throw new Error(error);
			}
		},
	},
});
